<template>
  <div>
    <app-form :pageHeader="pageHeader"
        :id="id"
        :submitButtonName="submitButtonName"
        :isActive="isActive"
        ref="appForm"
        @submit="submit">
    </app-form>
  </div>
</template>

<script>
import AppForm from '@/components/form/AppForm';

export default {
  data () {
    return {
      pageHeader: 'Edit app',
      submitButtonName: 'edit',
      isActive: true,
      failMessage: '',
      successMessage: 'Edit app success',
      // List of field
      id: this.$route.params.id
    };
  },
  components: {
    AppForm
  },
  methods: {
    setDefault () {
      this.appName = '';
      this.remark = '';
      this.failMessage = '';
    },
    submit (value) {
      this.setDefault();
      // edit app
      this.appName = value.appName;
      this.remark = value.remark;
      const json = { appName: this.appName, remark: this.remark };
      this.isActive = false;
      this.axios.post(process.env.VUE_APP_SERVICE_HOST + '/app/edit/' + this.id, json)
        .then(response => {
          this.$refs.appForm.showSuccess(this.successMessage);
        })
        .catch(error => {
          this.isActive = true;
          if (error.response) {
            const data = error.response.data;
            this.failMessage = data.topic;
            this.$refs.appForm.setFieldError(data.fields);
          } else {
            this.failMessage = error.message;
          }
          this.$refs.appForm.showFail(this.failMessage);
        });
    }
  }
};
</script>
